import Card from './card'
import ButtonAdd from './elements/buttonAdd'
import TextBox from './elements/textBox'
export default function CardList(props, state) {
  state = {
    cards: []
  }
  return (
    <div>
      <p>{props.title}</p>
      {
        state.cards.map((card) => {
          <Card content={card}/>
        })
      }
      <ButtonAdd/>
      <TextBox/>
    </div>
  )
}